
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}







































.readMessage{
  height: 500px;
  width: 300px;
  border: 1px solid black;  
  margin: auto;
}
ul{
  list-style: none;
  margin:0px;
  padding:0px;
  text-align:left;
}
#content{    
  display: inline-block;
  position: relative;    
	word-wrap: break-word;	
  background: #3c81fa;
	max-width: 300px;
	padding:5px;
  border-radius: 10px;
}
#name{
  font-weight: bold;
  display: block;
}
